.trailcard {
	background-color: #eaebec;
	color: #5f5f5f;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-webkit-box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.08);
	        box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.08);
	padding: 12px 18px;
	border: 1px solid #dbdbdb;
	border-radius: 5px;
	margin-bottom: 20px;
  margin-top: 20px;
  width: 100%;
}

.trailcard .row {
  margin: 0px;
}

 .img-container {
  height:200px;
  width: 200px;
  overflow: hidden;
  text-align: left;
  background:#6CADDC;
  margin: 0 0 0 -15px;
}
.img-container > img {
  height:100%;
  width: 100%;
  overflow: hidden;
}

.trailcard  h2 {
  font-size: 22px;
  color: #009966;
}

.trailcard li {
  list-style-type: none;
}



